@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family:"Raleway", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Primary Biru = #e5f3ed */
/* Font white = #ffffff */


.App{
  font-size: 14px;
  background-color: #ffffff;
  color:#616161;
  font-size: 14px;
}

html{
  scroll-behavior: smooth;
  
}

.navbar-top{
  background-color:#FFFFFF !important;
  box-shadow: 0px -8px 20px 0px black;
}

.navbar-brand{
  font-size: 14px;
  
}

.menu-top{
  color: #616161 !important;
  border: 1px solid#ffffff;
}

.menu-top:hover{
  border-bottom: 1px solid #616161;
  -webkit-transition: 350ms ease-in-out;
  transition: 350ms ease-in-out;

}
.navbar-dark .navbar-toggler-icon{
  background-image: ('./assets/favicon.ico') !important;
}

.text-registration {
  color: #f57600
}

.btn-registration {
  background-color: #f57600 !important;
  border: 1px solid #f57600;
}

.btn-registration:hover,.btn-registration:active,.btn-registration:focus {
  background-color: #f57600 !important;
  border: 1px solid #f57600;
}

.btn-outline-primary{
  color:#4a88cf !important;
  border: 1px solid #4a88cf;
}
.btn-outline-primary:hover,.btn-outline-primary:active{
  background-color: #4a88cf !important;
  border: 1px solid #4a88cf;
  color: #ffffff !important;
  -webkit-transition: 150ms ease-in-out;
  transition: 150ms ease-in-out;
}

.tab-agenda{
  color:#4a88cf !important;
  border: 1px solid red;
  cursor: pointer;
}
.tab-agenda-active{
  background-color: #4a88cf !important;
  color: #ffffff !important;
  -webkit-transition: 350ms ease-in-out;
  transition: 350ms ease-in-out;
}
.agenda-container{
  background-color: #fafafa;
}

iframe{
  box-shadow: 1px 1px 5px 0px black;
}

.Page{
  -webkit-animation: Open 1s ease-in-out;
          animation: Open 1s ease-in-out;
}

@-webkit-keyframes Open{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@keyframes Open{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

p{
  line-height: 2.5
}
.dropdown-item:hover{
  background-color: white !important;
  color: #343A40 !important;
}

.hotel-link{
  text-decoration: none !important;
  color: black !important;
}

.hotel-link{
  box-shadow: 3px 3px 10px 0px #9E9E9E;
  -webkit-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
}
.img-hotels{
  box-shadow: 3px 3px 10px 0px #9E9E9E;
}



.cfpshow{
  opacity: 0;
  display: block;
  -webkit-animation-name: cfpshow;
          animation-name: cfpshow;
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
  -webkit-animation-delay: 350ms;
          animation-delay: 350ms;
  -webkit-animation-timing-function:cubic-bezier(0.57, 0.21, 1, 1);
          animation-timing-function:cubic-bezier(0.57, 0.21, 1, 1);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.cfphidden{
  display: none;
}

@-webkit-keyframes cfpshow{
  0%{
    display: block;
    opacity: 0;
  }
  50%{
    opacity: 0.5;
  }
  100%{
    display: none;
    opacity: 1;

  }
}

@keyframes cfpshow{
  0%{
    display: block;
    opacity: 0;
  }
  50%{
    opacity: 0.5;
  }
  100%{
    display: none;
    opacity: 1;

  }
}

.cfptitle{
  cursor: pointer;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}
.cfptitle:hover > h5 > i{
  color: #dc3545;
  text-shadow: 1px 1px 1px #616161;
  -webkit-transition: 200ms ease-out;
  transition: 200ms ease-out;

}

.fa-minus{
  color: #dc3545;
  text-shadow: 1px 1px 1px #616161;
 
}

.btn-rounded{
  border-radius: 4vh;
}

.link-back{
  height: 8vh;
}
.back-link{
  text-decoration: none !important;
  color: #C82333;
  font-size: 12px;
  -webkit-animation-name: linkback;
          animation-name: linkback;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes linkback{
  from{
    color:red;
  }
  to{
    color:#616161;
    
  }
}

@keyframes linkback{
  from{
    color:red;
  }
  to{
    color:#616161;
    
  }
}

.sponsor-title{
  background-color: #4a88cf;
  color: #fff;
}

.registration-banner{
    color: #333;
    background-color: #ffefe2;
    border-color: white;
}

